// SignUp.jsx

import React, { useState, useEffect } from 'react';
import './style.css';
import { useNavigate } from 'react-router-dom';

const stepTitles = [
    "   !",
    "  !",
    " !",
    " !",
    " !"
  ];

  const initialFormData = {
    name: '',
    gender: 0,
    grade: 1,
    classNum: 1,
    username: '',
    password: '',
    phone: '',
    verificationCode: ''
  };
  
  function SignUp({setLoginState}) {
    const [step, setStep] = useState(0);
    const [formData, setFormData] = useState(initialFormData);
    const [error, setError] = useState('');
    const [codeSent, setVerifyStatus] = useState(false);
    const navigator = useNavigate();
  
    const totalSteps = 5;
  
    const handleChange = (e) => {
      const { name, value } = e.target;
      setFormData(prevData => ({
        ...prevData,
        [name]: value
      }));
    };
  
    useEffect(() => {
        validateStep(true)
    }, [formData]);
  
    const validateStep = (live = false) => {
        setError('');
        switch(step) {
          case 0:
            if (!live && !formData.name.trim()) {
              setError(' .');
              return false;
            }
            break;
          case 1:
            //      
            break;
          case 2:
            if (!live && !formData.username.trim()) {
              setError(' .');
              return false;
            }else if(!/^[A-Za-z0-9]{4,12}$/.test(formData.username)){
              setError(' 4~12  ,   .');
              return false;
            }
            break;
          case 3:
            if (!live && !formData.password.trim()) {
              setError(' .');
              return false;
            }else if(!/^[A-Za-z0-9!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]{6,10}$/.test(formData.password)){
                setError(' 6~10  ,   ');
                return false;
              }
            break;
          case 4:
            if (codeSent && (!live && !formData.phone.trim() || !live && !formData.verificationCode.trim())) {
              setError('   .');
              return false;
            }else if(!/^010\d{8}$/.test(formData.phone)){
                setError(' 010XXXXXXXX !');
                return false;
            }else if(codeSent && !/^\d{6}$/.test(formData.verificationCode)){
                setError(' 6 !');
                return false;
            }
            break;
          default:
            break;
        }
        return true;
      };
  
    const handleNext = () => {
      if (validateStep() && !error) {
        setStep(step + 1);
      }
    };
    const handlePrev = () => {
        setError('')
        setStep(step-1)
    };

    const sendCode = async () => {
        if(step == 4 && !error){
          const res = await fetch(`http://api.bubble.jyhs.kr/api/auth/code`, {
            "method": "POST",
            "headers": {
              "Content-Type": "application/json"
            },
            "body": JSON.stringify(
              {
                phone: formData.phone
              }
            )
            
            
          });
          let json = await res.json();
          console.log(json)
          if(json.success){
            setVerifyStatus(true);
            
          }else{
            alert(json.message);
          }
        }
    }

  const handleSignUp = async () => {
    if(step == 4 && !error){
      const res = await fetch(`http://api.bubble.jyhs.kr/api/auth/signup`, {
        "method": "POST",
        "headers": {
          "Content-Type": "application/json"
        },
        "body": JSON.stringify(
          {
            username: formData.username,
            name: formData.name,
            pw: formData.password,
            gender: formData.gender,
            grade: formData.grade,
            class: formData.classNum,
            phone: formData.phone,
            code: formData.verificationCode
          }
        )
        
        
      });
      let json = await res.json();
      console.log(json)
      if(json.success){
        localStorage.setItem("token", json.data.token);
        setLoginState(true);
      }else{
        alert(json.message);
      }
      
    }
  };
  return (
    <div className="ViewScreen">
      <div className="SignUp">
        <div className="container">
          <h1>Bubble   !</h1>
          <div className="step-info">
            <p className="step-title">{stepTitles[step]}</p>
            <p className="step-progress">{[" !", "    !", ",   !", "  !", " !"][step]} {step + 1}/{totalSteps}</p>
          </div>
          {error && <p className="error-message">{error}</p>}
          <div className="form-container" style={{ transform: `translateX(-${(100 / totalSteps) * step}%)` }}>
            <div className="form-step">
              <input type="text" name="name" placeholder="" value={formData.name} onChange={handleChange} />
              <div className="gender-radio">
                <input
                  id="male"
                  type="radio"
                  name="gender"
                  value={0}
                  checked={formData.gender === '0'}
                  onChange={handleChange}
                />
                <label htmlFor="male"></label>
                <input
                  id="female"
                  type="radio"
                  name="gender"
                  value={1}
                  checked={formData.gender === '1'}
                  onChange={handleChange}
                />
                <label htmlFor="female"></label>
              </div>
            </div>
            <div className="form-step">
              <select name="grade" value={formData.grade} onChange={handleChange}>
                <option value={1}>1</option>
                <option value={2}>2</option>
                <option value={3}>3</option>
              </select>
              <select name="classNum" value={formData.classNum} onChange={handleChange}>
                {
                  new Array(11).fill(1).map((_, i) => (
                    <option key={(i+1).toString()} value={(i+1)}>{(i+1).toString()}</option>
                  ))
                }
              </select>
            </div>
            <div className="form-step">
              <input type="text" name="username" placeholder="" value={formData.username} onChange={handleChange} />
            </div>
            <div className="form-step">
              <input
                type="password"
                name="password"
                placeholder=""
                value={formData.password}
                onChange={handleChange}
              />
            </div>
            <div className="form-step">
              <div className="phone-verification">
                <input type="text" name="phone" placeholder=" (!)" value={formData.phone} onChange={handleChange} />
                <button onClick={sendCode}> </button>
              </div>
              <input
                type="text"
                name="verificationCode"
                placeholder=""
                value={formData.verificationCode}
                onChange={handleChange}
                disabled={!codeSent}
              />
            </div>
          </div>
          {step < totalSteps - 1 && (
            <div className="navigator">
              {
                step > 0 ?
                <button onClick={handlePrev}>
                  
                </button>
                : <button onClick={() => navigator(-1)}>
                   
                </button>
              }
              <button onClick={handleNext}>
                
              </button>
            </div>
          )}
          {step === totalSteps - 1 && (
            <div className="navigator">
              <button onClick={handlePrev}>
                
              </button>
              <button onClick={handleSignUp}>
                
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default SignUp;